/* Register pages */

.register-page {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.register-tabs-container {
    width: 100%;
    height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}


.register-tabs {
    width: 100%;
    height: 100%;
    font-size: 5.5vw;
    border: none;
    color: var(--clear);
    cursor: pointer;
}

.register-business {
    background-color: var(--secondary);
}

.register-asso {
    background-color: var(--background);
    color: var(--secondary)
}

.register-form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 30px;
}

.register-form label {
    color: var(--clear);
    font-size: 4.5vw;
    margin: 10px 0;
}

.register-asso label {
    color: var(--secondary) 
}

.register-form > input {
    box-sizing: border-box;
    width: 100%;
    height: 35px;
    margin-bottom: 20px;
}

.upload-logo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
}

.upload-logo > input {
    color: var(--clear)
}

.upload-logo > button {
    border: none;
    margin-right: 20px;
    background-color: var(--clear);
}

.upload-logo img {
    width: 10vw;
}

.upload-logo p {
    color: var(--clear);
    font-size: 4.5vw;
    
}

.register-asso .upload-logo p {
    color: var(--secondary) 
}

.register-form textarea {
    box-sizing: border-box;
    width: 100%;
    height: 10vh;
    font-size: 4vw;
    padding: 10px;
    margin-bottom: 20px;
}

.register-form > select {
    margin-bottom: 20px;
}

.address-nums {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.address-nums > input {
    width: 20vw;
}

.address-city {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.address-city > input {
    display: inline;
    width: 30vw;
}

.date-input {
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 80vw;
}

.register-form p {
    color: var(--clear);
    font-size: 4.5vw;
}

.register-asso .register-form p {
    color: var(--secondary);
}

.register-form p > span {
    font-weight: 900;
}


.main-color-button {
    background-color: var(--background);
    color:  var(--secondary);
}

.input-button {
    border: none;
    font-size: 5vw;
    margin-top: 20px;
    padding: 0;
    height: 40px;
}

.hour {
    width: 100px;
}

.secondary-color {
    color: var(--secondary)
}

@media screen and (min-width: 1000px) {
    .register-tabs-container {
        height: 80px;
    }
    
    .register-tabs {
        font-size: 30px;
    }

    .register-form {
        width: 700px;
    }

    .register-form label {
        font-size: 30px;
    }
    
    .register-form textarea {
        height: 10vh;
        font-size: 20px;
    }
    
