    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.search-input > label {
    color: var(--secondary);
    font-size: 5vw;
    margin-bottom: 20px;
}

.search-input > input {
    width: 95%;
}

.filter-button {
    border: none;
    width: 20vw;
    padding: 5px;
    margin-left: 20px;
    background-color: var(--secondary);
    color: var(--background);
    cursor: pointer;
}

.filter-options {
    display: flex;
    flex-wrap: wrap;
    padding: 5px 20px;
}

.filter-options > label {
    padding: 5px 0;
    width: 38vw;
    font-size: 4.5vw;
    color: var(--text);
}

.filter-options input {
    margin-right: 10px;
}

@media screen and (min-width: 1000px) {

    .search-page {
        align-items: center;
    }
    
    .search-input {
        padding: 120px 50px 30px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }
    
    .search-input > label {
        font-size: 2vw;
        margin-bottom: 50px;
        text-align: center;
    }
    
    .search-input > input {
        width: 30vw;
        height: 35px;
        font-size: 20px;
    }
    
    .filter-button {
        width: 10vw;
        margin: 0;
        margin-bottom: 20px;
        font-size: 18px;
    }
    
    .filter-options {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
        padding: 20px 20px;
        width: 90%;
    }
    
    .filter-options > label {
        width: 15%;
        font-size: 20px;
    }
    
    .filter-options input {
        margin-right: 10px;
    }

    .search-page > .card-list > ul {
        justify-content: space-between;
    }
}

/* Details page  */
.details-page {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

}

.description-container {
    margin: 0 20px
}

.back-button-details {
    position: absolute;
    right: 20px;
    top: 15px;
}

.details-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 10px
}

.details-info > p:first-child {
    background-color: var(--clear);
    color: var(--text);
    padding: 8px;
    min-height: 10vh;
    width: 300px;
    border-radius: 5px;
    margin-bottom: 30px;
}

.details-info > h3 {
    color: var(--secondary);
    font-size: 5vw;
    margin: 20px 0 5px;

}

.details-less-description > h3 {
    color: var(--secondary);
    font-size: 5vw;
    margin: 20px 0 5px;

}
.details-info h4, .details-info p {
    font-size: 4.5vw;
    color: var(--text);
    margin: 20px 0 6px; 
    line-height: 1.6;
}

.details-info h4 {
    font-weight: 900;
}
.details-info > p {
    margin-bottom: 20px;
}

.details-page > button {
    align-self: center;
    bottom: -10px
}

.asso-details {
    height: 100vh;
}

.details-products h3:first-child {
    color: var(--secondary);
    margin: 40px 0 -10px;
    font-size: 5vw;
}

@media screen and (min-width: 1000px){ 
    .details-page {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .details-info {
        padding-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100vw
    }
    
    .details-info > p:first-child {
        background-color: var(--clear);
        color: var(--text);
        padding: 10px 30px;
        width: 90%;
        min-height: 6vh;
        min-width: 75vw;
        border-radius: 5px;
        margin-bottom: 0

    }
    
    .details-info > h3 {
        color: var(--secondary);
        font-size: 2.5vw;
        margin: 20px 10px 5px;
    
    }
    
    .details-less-description > h3 {
        font-size: 2.5vw;
    }

    .details-info h4,.details-info  p {
        font-size: 1.9vw;
        color: var(--text);
        margin: 20px 10px 0; 
    }
    
    .details-info > h4 {
        font-weight: 900;
    }
    .details-info > p {
        margin-bottom: 10px;
    }
    
    .details-page > button {
        align-self: center;
        bottom: -10px
    }
    
    .asso-details {
        height: 100vh;
    }

    .details-products h3:first-child {
        color: var(--secondary);
        margin: 40px 10px -10px;
        font-size: 2.5vw;
    }

    .details-less-description {
            display: flex;
            flex-direction: column;
            padding: 10px;
